import React, {useState} from "react"
import "./index.css"



const database = {
    username : "kehindeayodele",
   password : "12345"
 }

 const LogIn = () => {

     const [username, setUsername]= useState("")
     const[password, setPassword]=useState("")
     const [showError,setError]=useState("")
     const submit = () => {
     if  (username !== (database.username) && password !==(database.password) ){
        setError("you have enter incorrect password or incorrect username")
     }else{window.location.href="../Home"}
    
     }


      return(
          <>
          <div className="wrapper">
             
              <div className="form_container">
                  <div className="input_container">
                  <input className="input" type="text" placeholder="username" onChange = { (e) => setUsername(e.target.value)} /> <br />
                  <input className="input" type="password" placeholder="password"onChange = { (e) => setPassword(e.target.value)}  />  
                  <input className="button" type="button" value="LogIn" onClick = { () => submit () } />
                  </div>
              </div>
              {showError && <div>{showError}<br></br>
              Username : {username} <br></br>
            Password : {password}
              </div>}
          </div>
          </>
      )
 }


 export default LogIn